body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
  text-align: center;
}

.title {
  font-size: 1.5em;
}
.Issue-view {
    font-size: large;
    padding: 0 30px;
  }
.horizPanels {
  display: flex;
  flex-direction: row;
}

.leftPanel {
  min-width: 250px;
  border-right: solid 1px grey;
}

.vertPanels {
  display: flex;
  flex-direction: column;
}

.topPanel {
  background-color: #dddddd;
  padding-bottom: 20px;
}

.topPanel h3 {
  margin: 0;
  padding-top: 20px;
  padding-left: 20px;
  cursor: pointer;
}

h3 .menu-icon {
  padding-top: 5px;
  padding-right: 10px;
}
.App {
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
  text-align: center;
}

.App-title {
  font-size: 1.5em;
}

a {
  color: #428bca;
  text-decoration: none;
}

a:visited {
  color: #428bca;
}

